.woocommerce .woocommerce-breadcrumb{color:var(--dark-red);margin:0;padding:16px 0}.woocommerce .woocommerce-breadcrumb a{color:var(--gray);text-decoration:none;position:relative}.woocommerce .woocommerce-breadcrumb a::after{content:">";margin:0 8px}#breadcrumbs{margin:0;padding:16px 0;font-size:var(--subtitle-font-size);font-weight:var(--subtitle-font-weight);line-height:var(--subtitle-line-height);color:var(--gray)}#breadcrumbs .breadcrums-link-text{font-family:"Inter",sans-serif}#breadcrumbs span a{color:var(--gray);text-decoration:none;position:relative;font-family:"icomoon" !important;speak:never;font-style:normal;font-weight:normal;font-variant:normal;text-transform:none;line-height:1;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}#breadcrumbs span a::after{content:"";color:var(--gray);margin:0 8px}#breadcrumbs .breadcrumb_last{color:var(--dark-red)}